import {addPost, changeValue, DELETE_POST} from '../Actions/ActionTypes'

const initialState = {
    inputValue: 'fuck u state',
    postId: 3,
    posts: [
        {
            id: 1,
            likeCounter: 24,
            text: 'some post',
            profilePic: 'data:image/jpeg;base64,'
        },
        {
            id: 2,
            likeCounter: 10,
            text: 'another post',
            profilePic: 'data:image/jpeg;base64,'
        }
    ]
}
const postPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case addPost:
            return {
                ...state,
                postId:state.postId + 1,
                posts: [...state.posts, {
                    id: state.postId,
                    likeCounter: 0,
                    text: action.data,
                    profilePic: 'data:image/jpeg;base64,'
                }]
            }

        case changeValue: {
            return {
                ...state,
                inputValue: action.text
            }
        }
        case DELETE_POST: {
            return {
                ...state,
                posts: state.posts.filter(i => i.id !== action.id)
            }
        }
        default:
            return state
    }
}
export default postPageReducer
